.token.doctype,
.token.cdata {
  color: slategray; }

.token.punctuation {
  color: #999; }

.namespace {
  opacity: .7; }

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #905; }

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #690; }

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #a67f59;
  background: rgba(255, 255, 255, 0.5); }

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #07a; }

.token.function {
  color: #DD4A68; }

.token.regex,
.token.important,
.token.variable {
  color: #e90; }

.token.important,
.token.bold {
  font-weight: bold; }

.token.italic {
  font-style: italic; }

.token.entity {
  cursor: help; }

div.prism-show-language {
  position: relative; }

div.prism-show-language > div.prism-show-language-label[data-language] {
  color: black;
  background-color: #CFCFCF;
  opacity: 0.5;
  display: inline-block;
  position: absolute;
  bottom: auto;
  left: auto;
  top: 0;
  right: 0;
  width: auto;
  height: auto;
  font-size: 0.9em;
  border-radius: 0 0 0 5px;
  padding: 0.1em 0.5em;
  text-shadow: none;
  z-index: 1;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  -webkit-transform: none;
  -moz-transform: none;
  -ms-transform: none;
  -o-transform: none;
  transform: none; }

.invalid .typo__label {
  color: #f04124; }

.invalid .multiselect__tags {
  border-color: #f04124 !important; }

body {
  background: #fff;
  color: #35495E;
  font-family: 'Lato', Helvetica, sans-serif;
  text-decoration: none; }

.start {
  text-align: center;
  display: flex;
  background: linear-gradient(to left bottom, #8cc1f7 0%, #9cffd3 100%); }
  .start .typo__h1 {
    padding-top: 2.5rem;
    position: relative; }
  .start .typo__h3 {
    padding: 1.25rem 0; }
  @media only screen and (min-width: 640px) {
    .start {
      min-height: 100vh; } }
  .start .button {
    margin-bottom: 1.5rem; }

.start__list {
  padding-top: 1.875rem; }

.docs {
  text-align: left;
  padding-top: 3.75rem; }

.center-vertically {
  position: relative;
  align-self: center;
  width: 100%;
  min-height: 600px; }

.multiselect-example__container {
  margin: 0 auto 60px; }

.button {
  margin: 0 0.625rem; }

.logo {
  height: 4.375rem;
  margin-right: 1.25rem;
  vertical-align: middle;
  display: inline-block; }

.monterail-logo {
  width: 100px;
  height: 100px;
  margin-bottom: 1.875rem; }

.monterail-link {
  color: #EC1923; }

.button--github {
  padding-left: 3.75rem; }
  .button--github:before {
    content: url(data:image/svg+xml;base64,);
    left: 1.5625rem;
    position: absolute; }

.blank__link {
  color: inherit;
  text-decoration: none; }

a.version {
  position: absolute;
  bottom: -0.4375rem;
  font-size: 1.25rem;
  transform: translateX(-100%);
  text-decoration: none; }
  a.version:link, a.version:visited {
    color: #41B883; }
  a.version:hover {
    color: #374853; }
